.card-container {
  display: grid;
  grid-template-columns: repeat(1, 200px);
  row-gap: 46px;
  column-gap: 48px;
  margin: 0 auto;
  padding: 50px 40px;

  @media (min-width: 468px) {
    grid-template-columns: repeat(2, 200px);
  }

  @media (min-width: 768px) {
    grid-template-columns: repeat(3, 200px);
  }

  @media (min-width: 1024px) {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid $fifth-color;
  background: $primary-color;

  &__img {
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__name {
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $third-color;
  }

  &_numbers {
    margin-bottom: 16px;
    line-height: 14px;
    font-size: 10px;
    color: $fourth-color;
  }

  &__raiting {
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__raiting-review {
    font-size: 10px;
    line-height: 14px;
  }

  &__price {
    margin-bottom: 16px;
    display: flex;
    font-size: 12px;
    line-height: 16px;
    justify-content: space-between;
    color: $fourth-color;
  }

  &__price-cost {
    font-weight: 700;
    line-height: 18px;
    font-size: 16px;
    color: $third-color;
  }

  &__price-text {
    margin: 0;
    font-size: 12px;
    line-height: 18px;
  }

  &__button {
    padding-top: 11px;
    padding-bottom: 12px;
    font-size: 14px;
    border-radius: 5px;
    text-transform: uppercase;
    display: block;
    text-align: center;
    text-decoration: none;
    color: $primary-color;
    background-color: $secondary-color;
    font-weight: 700;
  }

  &__button:hover {
    border: 1px solid var(--blue-accent, $secondary-color);
    background-color: $primary-color;
    color: $secondary-color;
  }
}
